.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    text-align: center;
    min-height: 500px;
    margin: 35px 0px;

    .hero {
      max-width: 200px;
      border-radius: 50%;
    }
    p {
      max-width: 600px;
    }
    span {
      display: flex;
      gap: 25px;
      justify-content: center;
  
      a {
        margin: 0;
  
        img {
          width: 30px;
        }
      }
    }
  
    a {
      width: fit-content;
      align-self: center;
    }
  
    button {
      background-color: var(--btn-color);
      color: var(--btn-text-color);
      border: none;
      border-radius: 20px;
      width: 220px;
      height: 50px;
      font-size: 19px;
      font-weight: bold;
      box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
      transition: box-shadow 200ms ease-in-out, transform 200ms ease-in-out;
    }
  
    button:hover {
      transform: scale(1.05);
    }
  
    button:active {
      transform: translateY(2px);
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
    }
  }
  

  
  .description {
    max-width: 24ch;
  }
  
  .info {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  @media (width>= 800px){
    .container {
      flex-direction: row-reverse;
      align-items: center;
      justify-content: space-evenly;

      .hero {
        max-width: 350px;
        width: 350px;
    }
  }
  }

  @media (width>= 1400px){
    .container {
      flex-direction: row-reverse;
      align-items: center;
      justify-content: space-evenly;
      .hero {
        max-width: 400px;
        width: 400px;
    }
    }
  }